@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Radical";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Radical.woff2") format("woff2"), url("../fonts/Radical.woff") format("woff");
}
:root {
  font-size: 0.69445vw;
}
@media (max-width: 1024px) and (orientation: portrait) {
  :root {
    font-size: 3.125vw;
  }
}
@media (max-width: 1024px) and (orientation: portrait) and (min-width: 768px) {
  :root {
    font-size: 2.5vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  :root {
    font-size: 1vw;
  }
}
@media (max-width: 1024px) and (orientation: landscape) and (max-width: 768px) {
  :root {
    font-size: 2vw;
  }
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  position: relative;
  min-width: 320px;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.1;
  color: #ffffff;
  background-color: #5f458c;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (min-width: 2000px) {
  body {
    font-size: 18px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body {
    min-height: calc(var(--vh, 1vh) * 100);
    font-size: 1.4rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  body.menu-opened {
    overflow: hidden;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  body.menu-opened {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.svg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -100;
  opacity: 0;
}

.word-wrapper {
  display: inline-block;
  overflow: hidden;
}
.word-wrapper__item {
  display: inline-block;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0;
}

.page-content {
  position: relative;
  scroll-behavior: smooth;
}

.animation-screen {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-image: url("../img/scene.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% auto;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene@2x.png");
  }
}
@media (orientation: portrait) {
  .animation-screen {
    background-image: url("../img/scene-mob.png");
  }
}
@media (orientation: portrait) and (min-resolution: 144dpi), (orientation: portrait) and (min-resolution: 1.5dppx) {
  .animation-screen {
    background-image: url("../img/scene-mob@2x.png");
  }
}

.btn {
  padding: 0;
  margin: 0;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  position: relative;
  padding: 1.5rem 2.9rem;
  font-family: "Radical", "Arial", sans-serif;
  font-size: 2.4rem;
  font-weight: 400;
  line-height: 3rem;
  color: #5f458c;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: -0.04em;
}
.btn:focus {
  outline: none;
}
@media (min-width: 2000px) {
  .btn {
    padding: 15px 29px;
    font-size: 24px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .btn {
    padding: 1rem;
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  width: 100%;
  height: 100%;
  content: "";
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 3.5rem;
}
.btn:hover {
  color: #a67ee5;
}
.btn:focus {
  color: #a67ee5;
  outline: none;
}
.btn:focus::before {
  width: 103%;
  height: 108.5%;
  border-color: #a67ee5;
}
.btn:active, .btn:active:focus {
  color: #ffffff;
}
.btn:active::before, .btn:active:focus::before {
  width: 100%;
  height: 100%;
  background-color: #a67ee5;
  border-color: #a67ee5;
}

.chat {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  padding: 0 5.7rem;
  margin: 0 auto;
}
@media (min-width: 2000px) {
  .chat {
    padding: 0 57px;
  }
}
@media (orientation: portrait) {
  .chat {
    padding: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat {
    padding: 0;
  }
}

.chat__body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  max-width: 79.8rem;
  max-height: calc(100vh - 33.9rem);
  padding: 0 5.9rem 0;
  margin: 0 auto;
  overflow-y: auto;
}
@media (min-width: 2000px) {
  .chat__body {
    max-width: 798px;
    max-height: calc(100vh - 339px);
    padding: 0 59px 0;
  }
}
@media (orientation: portrait) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 22.4rem);
    padding: 0 0 0;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 25rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__body {
    max-height: calc(var(--vh, 1vh) * 100 - 16rem);
    padding: 0;
  }
}

.chat__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.chat__message {
  display: flex;
  flex: none;
  margin-bottom: 2rem;
  font-size: 2.4rem;
  line-height: 1.2;
  text-transform: uppercase;
}
@media (min-width: 2000px) {
  .chat__message {
    margin-bottom: 20px;
    font-size: 24px;
  }
}
@media (orientation: portrait) {
  .chat__message {
    font-size: 1.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message {
    font-size: 1.6rem;
  }
}
.chat__message p {
  min-width: 17rem;
  padding: 1.6rem 5.7rem;
  margin: 0;
  border-radius: 4rem;
}
@media (min-width: 2000px) {
  .chat__message p {
    min-width: 170px;
    padding: 16px 57px;
    border-radius: 40px;
  }
}
@media (orientation: portrait) {
  .chat__message p {
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    padding: 1rem 2.5rem;
    border-radius: 2rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__message p {
    min-width: 10rem;
    max-width: calc(100% - 5rem);
    padding: 1rem 2.5rem;
    border-radius: 2rem;
  }
}
.chat__message--outcoming {
  justify-content: flex-start;
}
.chat__message--outcoming p {
  width: 100%;
  color: #5f458c;
  word-break: break-word;
  background-color: #ffffff;
}
.chat__message--incoming {
  justify-content: flex-end;
  text-align: center;
}
.chat__message--incoming p {
  color: #ffffff;
  word-break: break-word;
  background-color: #a67ee5;
}

.chat__placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 17rem;
  height: 6rem;
  background-color: #a67ee5;
  border-radius: 3rem;
}
@media (min-width: 2000px) {
  .chat__placeholder {
    width: 170px;
    height: 60px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder {
    width: 10rem;
    height: 4rem;
  }
}
.chat__placeholder span {
  width: 1rem;
  height: 1rem;
  margin: 0 0.65rem;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 2000px) {
  .chat__placeholder span {
    margin: 0 6.5px;
  }
}
@media (orientation: portrait) {
  .chat__placeholder span {
    width: 0.6rem;
    height: 0.6rem;
    margin: 0 0.5rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .chat__placeholder span {
    width: 0.6rem;
    height: 0.6rem;
    margin: 0 0.5rem;
  }
}
.chat__placeholder--hidden {
  opacity: 0;
}

.chat__footer {
  display: flex;
  justify-content: flex-end;
  margin-top: auto;
}

.disclaimer {
  color: #5f458c;
  background-color: #ffffff;
  animation-name: none;
  animation-duration: 0.15s;
  animation-timing-function: ease-out;
  animation-delay: 0.15s;
  animation-fill-mode: both;
}
.is-loaded .disclaimer {
  animation-name: fadeIn;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer {
    font-weight: 400;
    color: #ffffff;
    background-color: transparent;
  }
}
.disclaimer ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  line-height: 1.2;
  text-transform: uppercase;
}
@media (min-width: 2000px) {
  .disclaimer ul {
    font-size: 12px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer ul {
    font-size: 1rem;
    opacity: 0.5;
  }
}
.disclaimer li {
  position: relative;
  padding: 0 0.6rem;
}
@media (min-width: 2000px) {
  .disclaimer li {
    padding: 0 6px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer li {
    padding: 0 0.4rem;
  }
}
.disclaimer li::before {
  position: absolute;
  top: 50%;
  left: 0;
  content: "/";
  transform: translate(-50%, -50%);
}
.disclaimer li:first-child::before {
  display: none;
}
.disclaimer a {
  display: inline-block;
  padding: 0.9rem 0 0.7rem;
  color: inherit;
  text-decoration: underline;
  vertical-align: top;
}
.disclaimer a:hover {
  opacity: 0.8;
}
.disclaimer a:focus {
  outline: none;
  opacity: 0.7;
}
@media (min-width: 2000px) {
  .disclaimer a {
    padding: 9px 0 7px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .disclaimer a {
    padding: 0.9rem 0;
  }
}

.form {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: 100%;
  margin-bottom: 1.2rem;
  font-size: 1.6rem;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  pointer-events: none;
  opacity: 0;
  transform: translateX(-50%);
}
@media (min-width: 2000px) {
  .form__label {
    margin-bottom: 12px;
    font-size: 16px;
  }
}
@media (orientation: portrait) {
  .form__label {
    pointer-events: auto;
    opacity: 1;
  }
}

.form__field-wrapper {
  --width: calc(100% - 18rem);
  position: absolute;
  right: 18rem;
  display: flex;
  align-self: flex-end;
  width: var(--width);
  overflow: hidden;
  border-radius: 3rem;
  animation-name: fadeIn, form__field-wrapper_expand;
  animation-duration: 0.5s, 0.5s;
  animation-timing-function: linear, ease-in-out;
  animation-delay: 0.35s, 0.85s;
  animation-fill-mode: both, both;
  will-change: width;
}
.screen--result .form__field-wrapper {
  --width: calc(100% - 22rem);
  right: 22rem;
}
@keyframes form__field-wrapper_expand {
  from {
    width: 6rem;
  }
  to {
    width: var(--width);
  }
}
@media (min-width: 2000px) {
  .form__field-wrapper {
    --width: calc(100% - 180px);
    right: 180px;
  }
  .screen--result .form__field-wrapper {
    --width: calc(100% - 230px);
    right: 230px;
  }
}
@media (orientation: portrait) {
  .form__field-wrapper, .screen--result .form__field-wrapper {
    --width: calc(100% - 5rem);
    right: 5rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__field-wrapper, .screen--result .form__field-wrapper {
    --width: calc(100% - 9rem);
    right: 9rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__field-wrapper, .screen--result .form__field-wrapper {
    --width: calc(100% - 5rem);
    right: 5rem;
  }
}

.form__field {
  position: relative;
  right: 0;
  flex: none;
  width: 100%;
  height: 6rem;
  padding: 0 5.6rem;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: #5f458c;
  text-transform: uppercase;
  border: 0;
  outline: none;
}
.form__field:focus {
  border: 1px solid #a67ee5;
}
@media (min-width: 2000px) {
  .form__field {
    height: 5rem;
    padding: 0 56px;
    font-size: 24px;
    border-radius: 30px;
  }
}
@media (orientation: portrait) {
  .form__field {
    height: 4rem;
    padding: 0 2.5rem;
    font-size: 1.6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__field {
    height: 4rem;
    padding: 0 2.5rem;
    font-size: 1.6rem;
  }
}

.form__button {
  margin-left: auto;
  animation-name: fadeIn;
  animation-duration: 0.35s;
  animation-fill-mode: both;
  will-change: opacity;
}
@media (orientation: portrait) {
  .form__button {
    position: relative;
    flex: none;
    width: 4rem;
    height: 4rem;
    padding: 0;
    font-size: 0;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button {
    position: relative;
    flex: none;
    width: 6rem;
    height: 6rem;
    padding: 0;
    font-size: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__button {
    width: 4rem;
    height: 4rem;
  }
}
.form__button svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 1.6rem;
  height: 1.8rem;
}
@media (min-width: 2000px) {
  .form__button svg {
    width: 16px;
    height: 18px;
  }
}
@media (orientation: portrait) {
  .form__button svg {
    display: block;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form__button svg {
    display: block;
  }
}

.form--result .form__field--wrapper {
  width: calc(100% - 31rem);
  overflow: hidden;
  border-radius: 3rem;
}
@media (min-width: 2000px) {
  .form--result .form__field--wrapper {
    width: calc(100% - 310px);
  }
}
@media (orientation: portrait) {
  .form--result .form__field--wrapper {
    width: calc(100% - 5rem);
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .form--result .form__field--wrapper {
    width: calc(100% - 9rem);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form--result .form__field--wrapper {
    width: calc(100% - 5rem);
  }
}

.game {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  padding: 14.6rem 0 6rem;
  margin: 0 auto;
}
@media (min-width: 2000px) {
  .game {
    padding: 146px 0 60px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game {
    padding: 5.9rem 0 4rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game {
    padding: 9rem 0 6rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game {
    padding: 5rem 0 1.5rem;
  }
}

.game__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 0.6rem;
  margin-bottom: 1.1rem;
}
@media (min-width: 2000px) {
  .game__header {
    padding-left: 6px;
    margin-bottom: 11px;
  }
}
@media (orientation: portrait) {
  .game__header {
    padding-right: 0;
    margin-bottom: 1.5rem;
  }
}

.game__title {
  margin: 0;
  font-family: "Radical", "Arial", sans-serif;
  font-size: 4.8rem;
  font-weight: 400;
  line-height: 0.9;
  text-transform: uppercase;
  letter-spacing: -0.04em;
}
.game__title .word-wrapper {
  margin-top: -2%;
}
.game__title .letter {
  padding-top: 2%;
  transform: translate3d(0, 110%, 0);
  will-change: transform;
}
.screen.active .game__title.active .letter {
  transform: translate3d(0, 0, 0);
}
@media (min-width: 2000px) {
  .game__title {
    font-size: 48px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__title {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__title {
    margin-bottom: 2rem;
    font-size: 3rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__title {
    margin-bottom: 1rem;
    font-size: 2rem;
  }
}

.game__counter {
  margin: 0;
  font-family: "Radical", "Arial", sans-serif;
  font-size: 4.8rem;
  font-weight: 400;
  line-height: 0.9;
  text-transform: uppercase;
  letter-spacing: 0;
}
@media (min-width: 2000px) {
  .game__counter {
    font-size: 48px;
  }
}
@media (min-width: 1024px) {
  .game__counter {
    margin-right: 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__counter {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .game__counter {
    margin-bottom: 2rem;
    font-size: 3rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .game__counter {
    margin-bottom: 1rem;
    font-size: 2rem;
  }
}
.game__counter span {
  display: inline-block;
  min-width: 5.5rem;
}
.game__counter span:first-of-type {
  text-align: right;
}
.game__counter span:last-of-type {
  text-align: left;
}

.game__body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.game__buttons {
  position: fixed;
  left: 0;
  z-index: 100;
  display: none;
  opacity: 0.5;
}

.game__button {
  display: block;
  margin: 0 30px 30px;
}

.intro {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  width: 100%;
  padding: 15.5rem 0 10rem;
}
@media (min-width: 2000px) {
  .intro {
    padding: 155px 0 100px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .intro {
    padding: 6rem 0 5.6rem;
  }
}
@media (max-width: 1024px) and (orientation: landscape) {
  .intro {
    padding: 16vh 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .intro {
    padding: 16vh 0 5vh;
  }
}

.intro__title .word-wrapper,
.intro__date .word-wrapper {
  margin-top: -2%;
}
.intro__title .letter,
.intro__date .letter {
  padding-top: 2%;
  transition-delay: 1.35s;
